import { ReactMatrixAnimation } from "react-matrix-animation";
import Typewriter from "./typewriter";

export default function IntroComonent() {
  return (
    <section id="home" className="intro route bg-image background">
      <div className="intro-content display-table">
        <div className="table-cell">
          <div className="container">
            <b className="intro-name">.  .</b>
            <b className="intro-title mb-4">   17   </b>
            <p className="intro-subtitle">
              <span className="text-slider-items"></span>
              <strong className="text-slider">
                <Typewriter texts={[
                  "     .",
                  "Spring,  , Angular, React, Flutter     .",
                  "       ."
                  ]} delay={60}></Typewriter>
              </strong>
            </p>
            <div className="m-auto flex matrix items-center justify-center">
              <ReactMatrixAnimation
                tileSize={13}
                backgroundColor={'#0a192f'}
                fadeFactor={0.5}
              />
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}